.icon {
	display: inline-block;
	width: 24px;
	height: 24px;
}

.settingsIcon,
.closeIcon {
	composes: icon;
	position: absolute;
	top: 10px;
	right: 10px;
	cursor: pointer;
	transition: opacity 0.2s ease-out;
	z-index: 5;
	fill: white;
}

.settingsIcon:hover,
.closeIcon:hover {
	opacity: 0.7;
}

.playIcon {
	composes: icon;
	cursor: pointer;
	transition: opacity 0.2s ease-out;
	width: 70vw;
	height: auto;
	max-width: 400px;
	margin: 0 auto;
	display: block;
}

.whitePlayIcon {
	composes: playIcon;
	fill: white;
}

.playIcon:hover {
	opacity: 0.7;
}



